.advantages__list {
  width: 320px;
  margin: 0 auto;
  padding: 0;
  padding-top: 37px;
  padding-right: 16px;
  padding-bottom: 30px;
  padding-left: 14px;
  list-style: none;
}

.advantages__item {
  position: relative;
  margin-bottom: 20px;
  padding-top: 74px;
  padding-right: 18px;
  padding-bottom: 30px;
  padding-left: 27px;
  border: 1px solid #ebebeb;
  border-left: 2px solid #00b2ff;

  &::before {
    content: "";
    position: absolute;
    left: 27px;
  }

  &--clock {
    &::before {
      top: 25px;
      width: 30px;
      height: 37px;
      background-image: url("../img/advantages__clock.svg");
    }
  }

  &--hired {
    &::before {
      top: 24px;
      width: 32px;
      height: 33px;
      background-image: url("../img/advantages__hired.svg");
    }
  }

  &--detective {
    &::before {
      top: 27px;
      width: 31px;
      height: 27px;
      background-image: url("../img/advantages__detective.svg");
    }
  }

  &--tag {
    &::before {
      top: 24px;
      width: 33px;
      height: 33px;
      background-image: url("../img/advantages__tag.svg");
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.advantages__headline-item {
  color: #00b2ff;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

.advantages__description-item {
  margin: 0;
}
